import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import * as moment from 'moment';


@Injectable({
  providedIn: 'root'
})
export class CalendarDataService {



  private urlPref = new gadgets.Prefs().getString('url');
  private url = `https://glacial-hollows-70580.herokuapp.com/ical/2020-09-14T00:00:00.000Z/2020-09-15T23:00:00.000Z?url=${this.urlPref}`;

  constructor(private http: HttpClient) { }

  getEvents(): Observable<any> {
    return this.http.get<any>(this.url).pipe(
      map(data => {

          let obj = new Object();
          let location: string;
          let summary: string;
          let startDate: string;
          let endDate: string;
          let statContainer: any;
          let eventContainer: any;
          const tempArr = [];

          if (data.events.length > 0){
            console.log('YES');
          }else{
            obj = {
              location: 'fargo',
              summary: 'my meeting',
              startDate: moment().format('MM DD YYYY hh:mm:ss'),
              endDate: moment().format('MM DD YYYY hh:mm:ss'),
            };

            statContainer = obj;
            tempArr[0] = statContainer;
          }

          if (data.occurrences.length > 0){
            eventContainer = data.occurrences.map((event: any) => {
              event.item.component[1].map((item: any) => {
                if (item[0] === 'location'){
                  location = item[3];
                }
                if (item[0] === 'summary'){
                  summary = item[3];
                }
                if (item[0] === 'dtstart'){
                  startDate = moment(item[3]).format('MM DD YYYY hh:mm:ss');
                }
                if (item[0] === 'dtend'){
                  endDate = moment(item[3]).format('MM DD YYYY hh:mm:ss');
                }

                obj = {
                  location,
                  summary,
                  startDate,
                  endDate
                };
              });
              return obj;
            });
          }

          const finalList = tempArr.concat(eventContainer);
          return finalList;
      })
    );
  }

}
