import { CssSelector } from '@angular/compiler';
import { Component, OnInit } from '@angular/core';
import { CalendarDataService} from '../Services/calendar-data.service';


@Component({
  selector: 'app-default-table',
  templateUrl: './default-table.component.html',
  styleUrls: ['./default-table.component.css']
})



export class DefaultTableComponent implements OnInit {

  displayedColumns: string[] = ['when', 'where', 'summary'];
  dataSource: any;
  value: any;
  styleJson: any;
  textStyle = new gadgets.Prefs().getString('fontColor');




  constructor(private calendarService: CalendarDataService) { }

  ngOnInit(): void {
    this.getEvents();
    this.getStyle();
  }

  getStyle(): void {
    const styleArray = this.textStyle.split(';');
    console.log(styleArray);
    styleArray.forEach(e => {
      if (e !== ''){
        console.log(e.split(':'));
        this.value = e.split(':');
        console.log(this.value);
        this.styleJson[this.value[0]] = this.value[1];
      }
      console.log(this.styleJson);
    });
  }

  getEvents(): void {
    const eventList = this.calendarService.getEvents()
      .subscribe(events =>  {
        this.dataSource = events;
      });
  }
}
